import React, { useState } from 'react';

const LecturerSettings: React.FC = () => {
  const [emailNotif, setEmailNotif] = useState(true);
  const [officeHours, setOfficeHours] = useState('Mon/Wed 13:00-15:00');
  return (
    <div className="max-w-3xl mx-auto space-y-6">
      <h1 className="text-2xl font-bold text-gray-900">Settings</h1>
      <div className="card space-y-4">
        <div className="flex items-center justify-between">
          <div>
            <div className="font-medium">Email Notifications</div>
            <div className="text-sm text-gray-500">Receive notifications about classes and grading</div>
          </div>
          <input type="checkbox" className="h-4 w-4" checked={emailNotif} onChange={e => setEmailNotif(e.target.checked)} />
        </div>
        <div>
          <div className="font-medium mb-1">Office Hours</div>
          <input className="input-field" value={officeHours} onChange={e => setOfficeHours(e.target.value)} />
        </div>
        <button className="btn-primary">Save Changes</button>
      </div>
    </div>
  );
};

export default LecturerSettings;
